import { Link, useNavigate, useOutletContext } from 'react-router-dom';
import { useForm } from "react-hook-form";
import { SelectBox, Input } from '../../components/FormElements';
import { Textarea } from '../../components/FormElements';
import Stepper from "../../components/Stepper";
import axios from 'axios';

function Checkout() {
  const navigate = useNavigate();
  const {  getCart } = useOutletContext();

  const {
    register,
    handleSubmit,
    control,
    formState: { errors },
  } = useForm({
    mode: 'onTouched',
  });



  const onSubmit = async (data) => {
    const { name, email, tel, address ,receipt} = data
    const form = {
      data: {
        user: {
          name,
          email,
          tel,
          address,
          receipt
        },

      }
    }
    try{
      const res = await axios.post(`/v2/api/${process.env.REACT_APP_API_PATH}/order`, form);
      await getCart();
      navigate(`/success/${res.data.orderId}`)
    }catch(error){
      console.log(error);
    }
   
  };
 
  return (

    <div className='container container-fluid-md'>
      {/* <Loading isLoading={isLoading} /> */}
      <div className='row justify-content-center'>
        <div className='col-12 col-md-10 col-lg-8' >
          <Stepper data={[
            { step: 1, content: '', done: true },
            { step: 2, content: '', done: true },
            { step: 3, content: '', done: false },
          ]}></Stepper>
          <form onSubmit={handleSubmit(onSubmit)}>
            <h4 className='text-white tablePadding bg-primary mb-0' style={{ borderTopLeftRadius: '30px', borderTopRightRadius: '30px' }}></h4>
            <div className="tablePadding checkout-border">
              <div className='my-3'>
                <Input
                  id='name'
                  type='text'
                  errors={errors}
                  labelText=''
                  register={register}
                  rules={{
                    required: '',
                    maxLength: {
                      value: 10,
                      message: ' 10',
                    },
                  }}
                ></Input>
              </div>

              <div className='my-3'>
                <Input
                  id='email'
                  labelText='Email'
                  type='email'
                  errors={errors}
                  register={register}
                  rules={{
                    required: 'Email ',
                    pattern: {
                      value: /^\S+@\S+$/i,
                      message: 'Email ',
                    },
                  }}
                ></Input>
              </div>
              <div className='my-3'>
                <Input
                  id='tel'
                  labelText=''
                  type='tel'
                  errors={errors}
                  register={register}
                  rules={{
                    required: '',
                    minLength: {
                      value: 6,
                      message: ' 6 '
                    },
                    maxLength: {
                      value: 12,
                      message: ' 12 '
                    }
                  }}
                ></Input>
              </div>
              <div className='my-3'>
                <Input
                  id='address'
                  labelText=''
                  type='address'
                  errors={errors}
                  register={register}
                  rules={{
                    required: '',
                  }}
                ></Input>
              </div>
              <div className='my-3'>
                <div className='form-group my-3'>
                  <Textarea
                    id='comment'
                    type='text'
                    errors={errors}
                    labelText=''
                    register={register}
                    placeholder=''
                  >
                  </Textarea>
                </div>
              </div>
              <div className='my-3'>
                <h4></h4>
                <div className="row">
                  <SelectBox
                    id="receipt"
                    control={control}
                    data={[
                      { label: '', value: 'electronic_invoice' },
                      { label: '', value: 'paper_invoice' },
                      { label: '', value: 'electronic_donation' },
                    ]}
                    placeholder=""
                    rules={{
                      required: {
                        value: true,
                        message: '',
                      },
                    }}
                  />
                </div>
              </div>
            </div>

            <div className='d-flex  mt-6 justify-content-center mb-7'>
              <Link className='mt-md-0  ' to='/cart'>
                <button className='btn btn-outline-secondary  btn-item-1'>
                  
                </button>
              </Link>
              <button
                type='submit'
                className='btn text-white ms-3 btn-primary  btn-item-1'
              >
                
              </button>
            </div>
          </form>

        </div>

      </div>
    </div>
  );
}

export default Checkout;