import { useEffect, useState } from 'react'
import { useSearchParams, useNavigate,Link } from 'react-router-dom'
import axios from "axios";
import Loading from "../../components/Loading";
import ProductsCard from '../../components/ProductsCard';

function Search() {
  //  useSearchParams  URL 
  const [searchParams] = useSearchParams()
  //  state 
  const [products, setProducts] = useState([])
  const [isLoading, setIsLoading] = useState(false)
  const navigate = useNavigate()
  //
  const searchString = searchParams?.get('query')

  useEffect(() => {
    const getFilteredProduct = async () => {
      setIsLoading(true)
      try {
        const res = await axios.get(`/v2/api/${process.env.REACT_APP_API_PATH}/products/all`);
        setProducts(
          // 
          res.data?.products?.filter((item) =>
            item.title.includes(searchString)
          )
        )
      } catch (error) {
        setProducts([])
        // Toast.fire({ icon: 'error', title: '' })
      } finally {
        setIsLoading(false)
      }
    }
    if (searchString) {
      //  getFilteredProduct 
      getFilteredProduct()
    } else {
      setProducts([])
    }
  }, [searchString])

  return (
    <>
      <div className="container mt-5">
        <Loading isLoading={isLoading} />
        <div className="row">
          <div className="col-md-3">
            {/* <Side /> */}
          </div>
          <div className="col-md-9">
            {searchString ? (
              <p className="mb-2">
                 {searchString}  {products.length} {' '}
              </p>
            ) : (
              <p className="mb-2"></p>
            )}
            <hr />
            {products?.length ? (
              ''
            ) : (
              <p>
                
              </p>
            )}
            <div className="row">
              {products.map((product) => {
                return (
                  <div className="col-md-4 col-6" key={product.id}>
                    <Link to={`/product/${product.id}`} style={{ textDecoration: 'none' }}>
                      <ProductsCard product={product} />
                    </Link>
                  </div>

                )

              })}
            </div>
            <button
              type="button"
              className="btn btn-primary mb-4 text-white"
              onClick={() => navigate('/')}
            >
              
            </button>
          </div>

        </div>
      </div>
    </>
  )
}

export default Search;