import { useEffect, useState, useCallback } from "react";
import Navbar from "../../components/Navbar";
import { Outlet } from "react-router";
import axios from "axios";
import MessageToast from "../../components/MessageToast";
import Footer from "../../components/Footer";
import { useDispatch} from 'react-redux';
import { createAsyncMessage, } from "../../slice/messageSlice";
import Loading from "../../components/Loading";  //  Loading 

function FrontLayout() {
  const [cartData, setCartData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const dispatch = useDispatch();
  
    //  useCallback  getCart 
  const getCart = useCallback(async () => {
    try {
      setIsLoading(true); //  isLoading  true
      const res = await axios.get(`/v2/api/${process.env.REACT_APP_API_PATH}/cart`);
      setCartData(res.data.data);
      setIsLoading(false); //  isLoading  false

    } catch (error) {
      setIsLoading(false);  //  false
      dispatch(createAsyncMessage({ success: false, message: error.response?.data?.message || '' }));
    }
  }, [dispatch]);

  useEffect(() => {
    getCart(); //  getCart 
  }, [getCart]);
  return (
    <>
      <Navbar cartData={cartData} />
      <MessageToast />
      {isLoading ? <Loading isLoading={isLoading} /> : null}
      <Outlet context={{ getCart, cartData }} ></Outlet>
      <Footer></Footer>
    </>
  )
}
export default FrontLayout